t\t\t\t})\r\n\t\t\t\t\t\t}else{\r\n\t\t\t\t\t\t\tthis.updateFileMap(path,path,fileType,\"\",\"show\");\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t\t\r\n\t\t\t\t\t}\r\n\t\t\t\t\t\r\n\t\t\t\t}\r\n\t\t\t}\r\n\t\t},\r\n\t\tdata() {\r\n\t\t\treturn {\r\n\t\t\t\tdelKey:\"\",\r\n\t\t\t\tshowDelDialog:false,\r\n\t\t\t\tfileMapStateTrack:0,\r\n\t\t\t\tgisqVideoUrl: \"\",\r\n\t\t\t\tshowMaskDialog: false,\r\n\t\t\t\tvideoHeight:240,\r\n\t\t\t\tvideoWidth:400,\r\n\t\t\t\tisHbuilder: false,\r\n\t\t\t\tcurrentValue: false,\r\n\t\t\t\tiTitle: \"类型选择\",\r\n\t\t\t\tiLeft: \"取消\",\r\n\t\t\t\tiShow: false,\r\n\t\t\t\tsheetItems: [{\r\n\t\t\t\t\t\tid: 1,\r\n\t\t\t\t\t\tname: \"拍照\",\r\n\t\t\t\t\t}, {\r\n\t\t\t\t\t\tid: 2,\r\n\t\t\t\t\t\tname: \"选取图片\",\r\n\t\t\t\t\t},\r\n\r\n\t\t\t\t],\r\n\t\t\t\taddedFileMap:new Map(),\r\n\t\t\t\tfileMap: new Map(),\r\n\t\t\t\tindex: 0,\r\n\t\t\t\tshowViewer: false,\r\n\t\t\t\tbigSrc: \"\",\r\n\t\t\t\tsrcList: [],\r\n\t\t\t\tzoomOptions: {\r\n\t\t\t\t\tinline: false,\r\n\t\t\t\t\tbutton: true,\r\n\t\t\t\t\tnavbar: false, //去除左右滑动切换\r\n\t\t\t\t\ttitle: false,\r\n\t\t\t\t\ttoolbar: {\r\n\t\t\t\t\t\tzoomIn: 0,\r\n\t\t\t\t\t\tzoomOut: 0,\r\n\t\t\t\t\t\toneToOne: 0,\r\n\t\t\t\t\t\treset: 0,\r\n\t\t\t\t\t\tprev: 0,\r\n\t\t\t\t\t\tplay: 0,\r\n\t\t\t\t\t\tnext: 0,\r\n\t\t\t\t\t\trotateLeft: 0,\r\n\t\t\t\t\t\trotateRight: 0,\r\n\t\t\t\t\t\tflipHorizontal: 0,\r\n\t\t\t\t\t\tflipVertical: 0\r\n\t\t\t\t\t},\r\n\t\t\t\t\ttooltip: false,\r\n\t\t\t\t\tmovable: true,\r\n\t\t\t\t\tzoomable: true,\r\n\t\t\t\t\trotatable: true,\r\n\t\t\t\t\tscalable: true,\r\n\t\t\t\t\ttransition: false,\r\n\t\t\t\t\tfullscreen: true,\r\n\t\t\t\t\tkeyboard: true,\r\n\t\t\t\t}\r\n\t\t\t}\r\n\t\t},\r\n\t\tmethods: {\r\n\t\t\tclickCancel:function(){\r\n\t\t\t\tconsole.log('点击了取消');\r\n\t\t\t\tthis.delKey=\"\";\r\n\t\t\t},\r\n\t\t\tclickDanger:function(){\r\n\t\t\t\tconsole.log('这里是danger回调')\r\n\t\t\t\tthis.deleteSelectedFile(this.delKey);\r\n\t\t\t\tthis.delKey=\"\";\r\n\t\t\t},\r\n\t\t\tclickConfirm:function(){\r\n\t\t\t\tconsole.log('点击了confirm');\r\n\t\t\t},\r\n\t\t\tgetFileType:function(path){\r\n\t\t\t\tvar fileType = \"picture\";\r\n\t\t\t\tif(!!path){\r\n\t\t\t\t\t\r\n\t\t\t\t\tvar types = path.split(\".\")\r\n\t\t\t\t\t\r\n\t\t\t\t\tif (types.length >= 2) {\r\n\t\t\t\t\t\tvar type = types[types.length - 1].toLowerCase();\r\n\t\t\t\t\t\tif (type === \"mp4\" || type === \"mov\" || type === \"avi\" || type === \"wmv\" || type === \"3gp\" ||\r\n\t\t\t\t\t\t\ttype === \"mkv\" || type === \"rmvb\" || type === \"webm\" || type === \"flv\" || type === \"qsv\") {\r\n\t\t\t\t\t\t\tfileType = \"video\";\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t\t\r\n\t\t\t\t\t}\r\n\t\t\t\t}\r\n\t\t\t\treturn fileType;\r\n\t\t\t\t\r\n\t\t\t},\r\n\t\t\tshowSheet: function() {\r\n\t\t\t\tthis.iShow = true;\r\n\t\t\t},\r\n\t\t\tclickOnSheet: function(obj) {\r\n\t\t\t\tif(this.isHbuilder==true){\r\n\t\t\t\t\ttry{\r\n\t\t\t\t\t\tvar cameraActivity = plus.android.importClass(\r\n\t\t\t\t\t\t\t\"com.zjzs.gisq.jetpack.aar_camara.CamaraActivity\");\r\n\t\t\t\t\t\t\tif (obj.id == 1) {\r\n\t\t\t\t\t\t\t\tthis.takePhoto();\r\n\t\t\t\t\t\t\t\t\r\n\t\t\t\t\t\t\t} else {\r\n\t\t\t\t\t\t\t\tthis.choosePhoto();\r\n\t\t\t\t\t\t\t}\r\n\t\t\t\t\t\t\t\r\n\t\t\t\t\t}catch(e){\r\n\t\t\t\t\t\t//to call H5's takePhoto\r\n\t\t\t\t\t\tif (obj.id == 1) {\r\n\t\t\t\t\t\t\tthis.takePhotoH5();\r\n\t\t\t\t\t\t\t\r\n\t\t\t\t\t\t} else {\r\n\t\t\t\t\t\t\tthis.choosePhotoH5();\r\n\t\t\t\t\t\t}\r\n\t\t\t\t\t}\r\n\t\t\t\t}else{\r\n\t\t\t\t\t//to call H5's takePhoto\r\n\t\t\t\t\tif (obj.id == 1) {\r\n\t\t\t\t\t\tthis.takePhotoH5();\r\n\t\t\t\t\t\t\r\n\t\t\t\t\t} else {\r\n\t\t\t\t\t\tthis.choosePhotoH5();\r\n\t\t\t\t\t}\r\n\t\t\t\t}\r\n\t\t\t\t\r\n\t\t\t},\r\n\t\t\ttakePhotoH5:function(){\r\n\t\t\t\tvar h5FileEl=this.$refs.gisqLibUploadInputFileH5;\r\n\t\t\t\tvar _self=this;\r\n\t\t\t\th5FileEl.onchange=function(e){\r\n\t\t\t\t\t_self.handleH5InputChange(this.files);\r\n\t\t\t\t}\r\n\t\t\t\th5FileEl.click();\r\n\t\t\t},\r\n\t\t\t